import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { createNumberMask } from "redux-form-input-masks";
import { hideModal } from "../../../actions/datamaster_action";
import { HiidenFiled, ReanderField } from "../../../components/helpers/field";
import ValidasiMasterGroup from "../../../Validasi/ValidasiMasterGroup";
import Loading from 'react-fullscreen-loading';

const maptostate = (state) => {
  if (state.datamaster.ShowModalGroup !== null) {
    return {
      initialValues: {
        id: state.datamaster.ShowModalGroup.id,
        kode_group: state.datamaster.ShowModalGroup.kode_group,
        nama_group: state.datamaster.ShowModalGroup.nama_group,
        harga: state.datamaster.ShowModalGroup.harga,
        persentase: state.datamaster.ShowModalGroup.persentase,
      },
    };
  }
};
const currencyMask = createNumberMask({
  prefix: "Rp. ",
  locale: "kr-KO",
});
class FormDataGroup extends Component {
  constructor(props) {
    super(props);
    this.state = {
      aktif: false,
    };
  }
  componentDidMount() {
    setTimeout(() => {
      if (this.props.isEdit) {
        document.getElementById("nama_group").focus();
      } else {
        document.getElementById("kode_group").focus();
      }
    }, 100);
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit} onKeyPress={(e) => { e.key === 'Enter' && e.preventDefault(); }} autoComplete="off">
        {
          this.props.isEdit ? <>
             <Field
              name="id"
              component={HiidenFiled}
              type="hidden"
              readOnly={this.props.isEdit}
            />
          </> : null
        }
        <Field
          tabIndex="1"
          id="kode_group"
          name="kode_group"
          component={ReanderField}
          type="text"
          label=" Kode Group"
          readOnly={this.props.isEdit}
          placeholder="Masukan Kode Group"
        />
        <Field
          tabIndex="2"
          id="nama_group"
          name="nama_group"
          component={ReanderField}
          type="text"
          label=" Nama Group"
          placeholder="Masukan Nama Group"
        />
        <Field
          tabIndex="3"
          name="harga"
          component={ReanderField}
          type="text"
          label="Harga"
          {...currencyMask}
          placeholder="Masukan Harga"
        />
        {/* <Field
          className="d-none"
          tabIndex="4"
          name="persentase"
          component={Gramasi}
          type="text"
          label="Persentase"
          onBlur={() => this.setState({ aktif: true })}
          placeholder="Masukan Persentase"
        /> */}
        <div className="row">
          <div className="col-12 text-right">
            <button
              className="btn btn-danger"
              onClick={() => this.props.dispatch(hideModal())}
              type="button"
              disabled={this.props.isLoading}
            >
              Batal
            </button>
            &nbsp;
            <button
              tabIndex="5"
              className={
                this.state.aktif ? "btn btn-success" : "btn btn-primary"
              }
              disabled={this.props.submitting}
              type="submit"
            >
              {this.props.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Menyimpan
                </>
              ) : (
                "Simpan Data"
              )}
            </button>
          </div>
        </div>
        {this.props.isLoading ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null}
      </form>
    );
  }
}

FormDataGroup = reduxForm({
  form: "ModalFormDataGroup",
  enableReinitialize: true,
  validate: ValidasiMasterGroup,
})(FormDataGroup);
export default connect(maptostate, null)(FormDataGroup);
